
<template>
  <main class="warp">
    <article class="article"></article>
    <aside>
      <div class="aside-item" v-for="item in list" :key="item.text">
        <h3>{{ item.text }}</h3>
        <ul>
          <li
            v-for="link in item.children"
            :key="link.id"
            :class="{ active: link.isChecked }"
          >
            <a
              href="javascript:;"
              @click="clickAsideItem(item.text, link.id)"
              >{{ link.title }}</a
            >
          </li>
        </ul>
      </div>
    </aside>
  </main>
</template>

<script>
import { reactive } from 'vue'
export default {
  name: 'Main',
  setup () {
    const list = reactive([
      {
        text: '开始',
        children: [
          {
            id: 1,
            title: '欢迎',
            isChecked: true
          },
          {
            id: 2,
            title: '快速指南',
            isChecked: false
          },
          {
            id: 3,
            title: '安装',
            isChecked: false
          },
          {
            id: 4,
            title: '基本用法',
            isChecked: false
          },
          {
            id: 5,
            title: '目录结构',
            isChecked: false
          },
          {
            id: 6,
            title: '配置',
            isChecked: false
          }
        ]
      },
      {
        text: '博客内容',
        children: [
          {
            id: 1,
            title: '头信息',
            isChecked: false
          },
          {
            id: 2,
            title: '撰写博客',
            isChecked: false
          },
          {
            id: 3,
            title: '使用草稿',
            isChecked: false
          },
          {
            id: 4,
            title: '创建页面',
            isChecked: false
          },
          {
            id: 5,
            title: '静态文件',
            isChecked: false
          },
          {
            id: 6,
            title: '常用变量',
            isChecked: false
          },
          {
            id: 7,
            title: '集合（Collections）',
            isChecked: false
          },
          {
            id: 8,
            title: '数据文件',
            isChecked: false
          },
          {
            id: 9,
            title: '资源',
            isChecked: false
          },
          {
            id: 10,
            title: '博客迁移',
            isChecked: false
          }
        ]
      },
      {
        text: '个性化',
        children: [
          {
            id: 1,
            title: '模板',
            isChecked: false
          },
          {
            id: 2,
            title: '永久链接',
            isChecked: false
          },
          {
            id: 3,
            title: '分页功能',
            isChecked: false
          },
          {
            id: 4,
            title: '插件',
            isChecked: false
          },
          {
            id: 5,
            title: '主题',
            isChecked: false
          },
          {
            id: 6,
            title: '附加功能',
            isChecked: false
          }
        ]
      }
    ])
    const clickAsideItem = (text, id) => {
      list.forEach((ele) => {
        ele.children.forEach((e) => {
          e.isChecked = false
        })
        if (ele.text === text) {
          ele.children.forEach((e) => {
            if (e.id === id) {
              e.isChecked = true
            }
          })
        }
      })
    }
    return { list, clickAsideItem }
  }
}
</script>
<style scoped lang="scss">
@import "@/style/variables.scss";
.warp {
  display: flex;
}
.article {
  flex: 1;
  min-height: 800px;
  margin-right: 30px;
  padding: 40px 40px 30px;
  border-radius: 10px;
  font-size: 21px;
  background: $bg-content;
}
.aside-item {
  ul,
  li {
    list-style: none;
    margin-left: 0;
    font-size: 16px;
  }
  li {
    line-height: 2;
    a {
      color: $color-yellow;
      &:hover {
        color: $color-hover;
        transition: all 0.15s;
      }
    }
  }
  li.active {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      left: -30px;
      top: 6px;
      border: 10px solid;
      border-color: transparent;
      border-left-color: $bg-content;
    }
  }
  h3 {
    display: inline-block;
    margin-left: -30px;
    padding: 9px 0 9px 30px;
    font-size: 18px;
    font-weight: 700;
    border-bottom: 1px solid $red;
  }
}
</style>
