<template>
  <header>
    <div class="warp">
      <div class="header-img">
        <img src="@/assets/logo.png" />
      </div>
      <nav class="nav">
        <ul>
          <li v-for="item in nav" :key="item.id">
            <a href="javascript:;" :class="{ active: item.isChecked }"
              @click="toggleNavItem(item.id)"
            >{{ item.text }}</a
            >
          </li>
        </ul>
      </nav>
    </div>
  </header>
</template>

<script>
import { ref } from 'vue'
export default {
  name: 'Header',
  setup () {
    const nav = ref([
      {
        id: 1,
        text: '首页',
        isChecked: true
      },
      {
        id: 2,
        text: '博客',
        isChecked: false
      },
      {
        id: 3,
        text: '关于',
        isChecked: false
      }
    ])
    const toggleNavItem = (id) => {
      nav.value.forEach(el => {
        el.isChecked = false
        if (id === el.id) {
          el.isChecked = true
        }
      })
    }
    return { toggleNavItem, nav }
  }
}
</script>
<style lang='scss' scoped>
@import "@/style/variables.scss";
.warp {
  display: flex;
  padding: 15px;
}
.header-img {
  overflow: hidden;
  img {
    height: 144px;
    width: auto;
  }
}
.nav {
  flex: 1;
  ul {
    margin: 0;
    padding: 0 0 0 75px;
    display: flex;
    height: 144px;
    line-height: 144px;
  }
  li {
    list-style: none;
    width: 90px;
    text-align: center;
  }
  a {
    padding: 9px 18px;
    font-weight: 900;
    font-size: 18px;
    border-radius: 6px;
    overflow: hidden;
    color: $color-yellow;
    text-shadow: none;
    transition: all 0.25s;
    &:hover {
      color: $color-hover;
      background: $bg-dark;
    }
  }
  a.active {
    background: $color-yellow;
    color: $color-active;
  }
}
</style>
